.event {
  padding-top: 60px;
  padding-bottom: 110px;
}

.event__item {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.event__item__block {
  border-radius: 6px;
  flex: 0 30%;
  margin-right: 10px;
  transition: box-shadow .2s linear;
  position: relative;
  box-shadow: 0 11px 55px #00000014;
}

.event__item__block:hover {
  box-shadow: 0 5px 15px #0000004d;
}

.event__item__block__image {
  height: 240px;
  overflow: hidden;
}

.event__item__block__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 6px 6px 0 0;
}

.event__item__block__date {
  z-index: 1;
  color: #fff;
  text-align: center;
  background-color: #086cf9;
  border-radius: 6px 0 0;
  padding: 9px 13px;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
  position: absolute;
  top: 0;
  left: 0;
}

.event__item__block__info {
  padding: 24px 24px 32px;
}

.event__item__block__info__what {
  color: #222;
  margin-bottom: 20px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px;
}

.event__item__block__info__else {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.event__item__block__info__else__time {
  max-width: 100%;
}

@media (max-width: 1080px) {
  .event__item {
    justify-content: center;
  }

  .event__item__block {
    flex: 0 35%;
    margin-bottom: 40px;
    margin-right: 25px;
  }

  .event {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}

@media (max-width: 900px) {
  .event {
    padding-top: 0;
    padding-bottom: 40px;
  }
}

@media (max-width: 620px) {
  .event__item__block {
    flex: 0 70%;
    margin-bottom: 25px;
    margin-right: 0;
  }

  .event__item__block__info__what {
    font-size: 18px;
    line-height: 22px;
  }

  .event__item__block__info__else {
    display: block;
  }
}

@media (max-width: 500px) {
  .event__item__block {
    flex: 0 80%;
  }

  .event__item__block__info__else {
    display: block;
  }
}

@media (max-width: 400px) {
  .event__item__block {
    flex: 0 100%;
  }
}

.statistics2 {
  background: #d3e5fe;
  padding: 65px;
}

.statistics2__item {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.statistics2__item__block:hover {
  transition: all .3s;
  transform: scale(1.09);
}

.statistics2__item__block__number {
  color: #222;
  text-align: center;
  margin-bottom: 16px;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 62px;
}

.statistics2__item__block__what {
  color: #555;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
}

@media (max-width: 1080px) {
  .statistics2__item__block__number {
    font-size: 32px;
    line-height: 35px;
  }

  .statistics2 {
    padding: 55px;
  }
}

@media (max-width: 620px) {
  .statistics2__item__block__number {
    margin-bottom: 12px;
    font-size: 24px;
    line-height: 30px;
  }

  .statistics2 {
    padding: 35px;
  }
}

@media (max-width: 500px) {
  .statistics2 {
    padding: 35px 0;
  }
}

@media (max-width: 390px) {
  .statistics2__item__block__number {
    margin-bottom: 8px;
    font-size: 18px;
  }

  .statistics2__item__block__what {
    font-size: 14px;
  }

  .event {
    padding-bottom: 15px;
  }
}

